<template>
  <div class="dispatch">
    <h2>調度控制台-表格式</h2>
    <div class="dispatch-main">
      <div class="dispatch-order">
        <h3>待調度</h3>
        <div class="dispatch-order-d" v-for="(item,index) in order">
          <div>{{item.SOrderNo}}</div>
          <div>{{item.FromAddr}}</div>
          <div>{{item.ToAddr}}</div>
        </div>
      </div>
      <div class="dispatch-car">
        <h3>已調度</h3>
        <div class="dispatch-car-d" v-for="(item,index) in car" v-if="item.DriverInfo" @click="item.open=!item.open;$forceUpdate();">
          <div class="dispatch-car-d-d">{{item.CarNo}} ({{item.DriverInfo.DriverName}})</div>
          <div class="dispatch-car-d-dis">
            <div class="dispatch-car-d-dis-d" v-for="(item2,index2) in get_dis_by_driverid(item)">{{item2.DespatchDetails[0].OrderDetails.SOrderNo}}</div>
          </div>
          <DriverStroke :order="get_dis_by_driverid(item)" v-if="item.open" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import moment from 'moment'
import DriverStroke from './DriverStroke'
export default {
  data() {
    return {
	  order:"",
	  dispatch:"",
	  car:""
	}
  },
  components: {DriverStroke},
  mounted:async function() {
    this.GLOBAL.show_black=true
	await this.all_ready()
	await this.get_data()
	this.GLOBAL.show_black=false
	console.log(moment().format())
  },
  watch:{
    async $route(to, from){
      this.GLOBAL.show_black=true
	  await this.get_data()
	  this.GLOBAL.show_black=false
    }
  },
  methods:{
    get_data:async function(x) {
      this.order=await this.Function.get_dispatch_order(moment().format("YYYY-MM-DD"))
	  this.dispatch=await this.Function.get_dispatch_allgroup(moment().format("YYYY-MM-DD"))
	  this.car=await this.Function.get_dispatch_car()
	  for(let i in this.dispatch) {
	    this.dispatch[i].time=moment(this.dispatch[i].DespatchDetails[0].OrderDetails.ReservationDate).format("HH:mm")
      }
	  for(let i in this.car) {
	    this.car[i].open=false
	  }
      console.log(this.order)
      console.log(this.car)
    },
    get_dis_by_driverid:function(x) {
      var arr=[]
      for(let i in this.dispatch) {
        if(x.DriverId==this.dispatch[i].DespatchDetails[0].Despatch.DriverInfoId) {
          arr.push(this.dispatch[i])
        }
      }
      return arr
    }
  }
}
</script>


